import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

function HeaderPanel(props) {
  const thiz = props.context;
  return(
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo03" aria-controls="navbarTogglerDemo03" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="navbar-brand" style={{cursor:'pointer'}}>
          <img src={logo} className="App-logo" alt="logo" style={{marginRight:"10px"}}/>
          B-create
        </div>
        <div className="collapse navbar-collapse" id="navbarTogglerDemo03">
          <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
            <li className="nav-item" >
            </li>
          </ul>
          <div className="my-2 my-lg-0 navbar-dark navbar-nav nav-link">
          </div>
          <div className="my-2 my-lg-0">
            <div className="text-light">{thiz.state.pageTitle[thiz.state.page]} ({thiz.state.page}/6)</div>
          </div>
        </div>
      </nav>
    );
}

function FooterPanel() {
	return(
      <nav className="navbar fixed-bottom navbar-expand-lg navbar-dark bg-dark">
        <div className="navbar-collapse" id="navbarTogglerDemo03">
          <div className="navbar-nav mr-auto mt-2 mt-lg-0 text-light" style={{color:"rgba(255,255,255,.5)"}}>
            B-create
          </div>
        </div>
      </nav>
		);
}

