/*

{thiz.state.currentUsername.length > 0 ? <div style={{color:"rgba(255,255,255,.5)"}}>{thiz.state.currentUsername}</div> : <a className="nav-link" href="#">Login</a>}
*/

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 1,
      age: -1,
      pageTitle: ["","Welcome","Demographics","Video Selection","Video Editing","Summary","Feedback"]
    }
  }

  nextPage = () => (event) => {
  	var new_page = this.state.page + 1;
    this.setState({page: new_page})
  }

  previousPage = () => (event) => {
  	var new_page = this.state.page - 1;
    this.setState({page: new_page})
  }

  handleAgeChange = () => (event) => {
    this.setState({age: event.target.value});
  }

  render() {
  	if (this.state.page === 1) {
    return (
      <div className="App">
        <HeaderPanel context={this}/>
        <div className="App-intro">
        	<div>
				Thank you for participating in our study.<br/>
				In this study, your task is to edit a given video with B-roll.<br/>
				You will be given a raw video footage and are asked to insert relevant B-roll.<br/>
				The total task should take about 15 minutes.<br/>
				You can opt out anytime during the study.
			</div>
        </div>
        <button onClick={this.nextPage()} className="btn btn-primary">In understand</button>
        <FooterPanel/>
      </div>
    );
	} else if (this.state.page === 2) {
	    return (
	      <div className="App">
	        <HeaderPanel context={this}/>
	        <div className="App-intro">
	        	<div>Just a couple of questions before we start..</div>
		        <DemographicsView context={this}/>
	        </div>
        	<button onClick={this.previousPage()} className="btn">Previous</button>
        	<button onClick={this.nextPage()} className="btn btn-primary">Next</button>
	        <FooterPanel/>
	      </div>
	    );
	} else if (this.state.page === 3) {
	    return (
	      <div className="App">
	        <HeaderPanel context={this}/>
	        <div className="App-intro">
		        <div>
					We pre-selected some raw video footage for you to edit.<br/>
					Select a video from the following list that you would like to edit.<br/>
					You can preview each video to select the video of your choice.<br/>
					Select a video that interests you most, but you can be creative so any choice is fine here!
				</div>
		        <VideoSelectionView context={this}/>
	        </div>
        	<button onClick={this.previousPage()} className="btn">Previous</button>
        	<button onClick={this.nextPage()} className="btn btn-primary">Next</button>
	        <FooterPanel/>
	      </div>
	    );
	} else if (this.state.page === 4) {
	    return (
	      <div className="App">
	        <HeaderPanel context={this}/>
	        <div className="App-intro">
        		Baseline Video Editor
				<EditingViewBaseline/>
	        </div>
        	<button onClick={this.previousPage()} className="btn">Previous</button>
        	<button onClick={this.nextPage()} className="btn btn-primary">Next</button>
	        <FooterPanel/>
	      </div>
	    );
	} else if (this.state.page === 5) {
	    return (
	      <div className="App">
	        <HeaderPanel context={this}/>
	        <div className="App-intro">
				<div>
					View your final video edit!
				</div>
				<ReviewView/>
				<div>
					Congratulations!<br/>
					You added 3 B-Roll to the video!<br/>
					Watch the full video now!
				</div>
	        </div>
        	<button onClick={this.previousPage()} className="btn">Previous</button>
        	<button onClick={this.nextPage()} className="btn btn-primary">Next</button>
	        <FooterPanel/>
	      </div>
	    );
	} else if (this.state.page === 6) {
	    return (
	      <div className="App">
	        <HeaderPanel context={this}/>
	        <div className="App-intro">
		          <div>We need your feedback! Please answer the following questions thoroughly:</div>
		          <FeedbackView/>
	        </div>
        	<button onClick={this.previousPage()} className="btn">Previous</button>
        	<button className="btn btn-primary">Done</button>
	        <FooterPanel/>
	      </div>
	    );
	}
  }
}

export default App;



